.bb-select {
    position: relative;
    padding: 0;

    &::before {
        content: '\f107';
        @include fa('light');
        font-size: 18px;
        color: $color-icon;
        @include vy;
        right: 10px;
    }

    @include respond-to(i5) {
        width: 132px;
    }

    &__reset {
        @include respond-to(xs) {
            width: 30px;
            padding: 11px 0;
            @include flexCenter;
            justify-content: flex-end;
            @include centre('y');
            right: -3px;
            z-index: z(dropdown);

            i {
                font-size: 18px;
            }
        }
    } //.bb-select__reset

    select {
        @extend %styleInput;
        padding: 11px 25px 10px 10px;
        width: 100%;
        // height: 100%;
        font-family: $f;
        font-size: 16px;
        color: $gray--darkest;
        appearance: none;

        &:focus {
            border-color: $color-border--lighten;
            box-shadow: none;
        }

        &.has-placeholder {
            font-family: $f-light-i;
            color: #999;

            option {
                font-family: $f;
                color: $color-text;
            }
        }
    } //.bb-select select

    option {
        padding: 0 10px;
    } //.bb-select option

    &__icon {
        width: 24px;
        height: 24px;
        @include flexCenter;
    } //.bb-select__icon

    &__ico {
        width: 24px;
        height: 24px;
        fill: $gray--dark;
        pointer-events: none;
    } //.bb-select__ico

    &--icon {
        display: none;
        max-width: 90px;
        height: 40px;
        line-height: 40px;
        background-color: #fff;
        position: relative;
        align-items: center;

        &::before {
            display: none;
        }

        .bb-select {

            &__icon {
                @include centre();
                z-index: z(fixed);
            }
        }

        select {
            max-width: 90px;
            width: auto;
            height: 0;
            padding: 38px 0 0;
            background-color: transparent;
            position: relative;
            z-index: z(fixed) + 1;

            &:hover,
            &:focus {
                border-color: $input-color-border--hover;
            }
        }
    } //.bb-select--icon
} //.bb-select

.select-color {

    &__ball {
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;

        @each $theme, $color in $text-colors {

            &.#{$theme} {
                background-color: $color;
            }
        }

        & + p {
            padding-left: 9px;
        }
    } //.select-color__ball

    &__line {
        width: 3px;
        height: 90%;
        @include centre('y');
        left: -1px;

        @each $theme, $color in $text-colors {

            &.#{$theme} {
                background-color: $color;
            }
        }
    } //.select-color__line

    &__item {
        width: 100%;
        height: 100%;
        @include flexCenter;

        p {
            max-width: 240px;
            @include text-overflow;
        }
    } //.select-color .custom-select__results--color

    &--palette {
        width: 100px;

        .select2-dropdown {
            width: 163px !important;
            padding: 5px;
        }

        .select2-results {

            &__options {
                display: flex;
                flex-wrap: wrap;
            }

            &__option {
                padding: 5px;
            }

            &__option[aria-selected='true']::after {
                font-weight: 400 !important;
                font-size: 11px !important;
                color: #fff !important;
                right: 9px !important;
            }

            &__option--highlighted[aria-selected] {
                background-color: transparent;
            }
        }
    } //.select-color.palette-mode
} //.select-color

$select-height: 40px;
$select-line-height: 39px;
$select-border-radius: $border-radius !default;
$select-border-color: $color-border--light !default;

.c-select {
    height: 40px;
    line-height: 39px;
    padding: 0 20px 0 10px;
    border: 1px solid $input-color-border;
    border-radius: $border-radius;
    cursor: pointer;
    position: relative;
    transition: all 0.35s ease;

    &::after {
        content: '\f107';
        @include fa('light');
        font-size: 17px;
        color: $color-text;
        @include centre('y');
        right: 8px;
    }

    &:hover {
        border-color: $input-color-border--hover;
    }

    &:focus {
        font-size: 16px;
        border-color: $color-border--lighten;
        box-shadow: $bxs-box;
    }

    &.is-open {
        box-shadow: $bxs-box;
        border-color: $color-border--lighten;

        &::after {
            content: '\f106';
        }

        .c-select {

            &__dropdown {
            }
        }
    }

    &__dropdown {
        background-color: #fff;
        border-color: $color-border--lighten;
        box-shadow: $bxs-box;
        max-height: 600px;
        width: 100%;
        position: absolute;
        top: 45px;
        left: 0;
        z-index: z(fixed);
        @include flexColumn;
    } //.c-select__dropdown

    &__placeholder {
        font-family: $f-light-i;
        font-size: 14px;
        line-height: 23px;
        color: $gray--light;

        @include respond-to(xs) {
            font-size: 16px;
        }
    } //.c-select__placeholder

    &__services {
        @include coverdiv;
        overflow-y: auto;
    }

    &__value {
    } //.c-select__value

    &__title {
    } //.c-select__title

    &__list {
    } //.c-select__list

    &__item {
    } //.c-select__item

    &__row {
    } //.c-select__row

    &__price {
    } //.c-select__price

    &__time {
    } //.c-select__time
} //.c-select
