.wrapper {

    &__inner {
        min-height: 100vh;
        height: 100%;
        overflow: hidden;
        position: relative;
        background: #fff;
        @include flexColumn;

        @include respond-to(sm) {
            left: 0;
            transition: left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);
            will-change: left;
        }
    } //.wrapper__inner

    &.mobile-nav--open {

        .wrapper__inner {
            overflow: hidden;
        }

        .mobile-nav {
            box-shadow: 0 1px 15px rgba(#000, 0.9);
            pointer-events: auto;
            @include trfX(0);
        }
    } //.wrapper.mobile-nav--open

    &.mobile-search--open {

        .mobile-search {
            display: block;
            z-index: 1002;
        }
    } //.wrapper.mobile-search--open

    &.header-not-fixed {

        .header {
            position: relative;
        }

        .main {
            padding-top: 0;
        }
    } //.wrapper.header-not-fixed

    &.bg-white {
        background-color: #fff;

        .wrapper__inner {
            background-color: #fff;
        }

        .header {
            box-shadow: none;
            border-bottom: 1px solid $color-border--lightest;
        }

        .main {
            background-color: #fff;
        }
    } //.wrapper.bg-white
} //.wrapper

.main {
    background-color: $color-bg--lightest;
    flex: 1;

    &.home,
    &.card,
    &.promo,
    &.order {
        background-color: #fff;
    }

    &.promo {
        @include respond-to(sm) {
            padding-top: 0;
        }
    }
} //.main

.bg {
    @each $theme, $color in $button-colors {

        &--#{$theme} {
            background-color: $color;
        }
    }

    @each $theme, $color in $bg-colors {

        &--#{$theme} {
            background-color: $color;
        }
    }
} //.bg

.section {
    margin-bottom: $section-indent;
    padding: $section-indent 0;

    &-gradient {
        background: $color-bg--accent,
            url('../img/icons/svg/bg-pattern-pink.svg');
        background-size: 125px;
        position: relative;

        &::before,
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: -22px;
            right: 0;
            left: 0;
            height: 32px;
            background: url('../img/icons/svg/wave-white.svg');
            background-size: cover;
        }

        &::after {
            transform: rotate(180deg);
            top: auto;
            bottom: -22px;
            content: '';
        }

        &--gray {
            background-color: $gray--lightest;
            background-image: url('../img/icons/svg/bg-pattern-gray.svg');
            background-size: 124px;
        }
    } //.section--gradient

    &--dark {
        color: #fff;

        .bb-title {
            color: #fff;

            &__sub {
                color: $gray--lightest;
            }
        }
    } //.section--dark

    &.about {
        margin-bottom: 0;
        padding-bottom: 20px;

        @include respond-to(xs) {
            margin-bottom: 0;
            padding: 30px 0 50px;
        }
    }

    &.description {
        padding: 0;
    }
} //.section

.bb-title {
    margin: 0 0 30px;
    text-align: center;

    &__title {
        font-family: $f;
        font-weight: 400;
        font-size: 32px;
    } //.bb-title__title

    &__sub {
        margin-top: 10px;
        font-family: $f-light;
        font-size: 18px;
        color: $gray--light;

        p {
            display: block;

            & + p {
                margin-top: 10px;
            }
        }
    } //.bb-title__sub

    &--devider {
        margin: 15px 0;
        @extend %textLight;
        font-size: 14px;
        line-height: 1;
        overflow: hidden;
        position: relative;

        display: table;
        white-space: nowrap;

        &::before,
        &::after {
            border-top: 1px solid $gray--lighten;
            content: '';
            display: table-cell;
            position: relative;
            top: 0.5em;
            width: 45%;
        }

        &::before {
            right: 1.5%;
        }

        &::after {
            left: 1.5%;
        }
    } //.bb-title--devider
} //.bb-title

.page-title {
    display: block;
    margin-bottom: 30px;
    font-family: $f-medium;
    font-weight: 500;
    font-size: 25px;
    color: $gray--darkest;

    @include respond-to(sm) {
        font-size: 22px;
    }

    @include respond-to(xs) {
        margin: 20px 0 30px;
    }

    &__wrap {
        margin-bottom: $gutter;
        @include flexCenter;

        @include respond-to(xs) {
            margin-bottom: 15px;
        }

        .button-link {
            font-family: $f-light;
            font-size: 15px;
            margin-left: auto;
        }

        .page-title {
            margin-bottom: 0;
        }

        .catalog-content__result {
            margin-left: auto;
            @include respond-to(xs) {
                margin: 0 auto;
            }
        }

        &--column {
            flex-direction: column;
            align-items: flex-start;

            .page-title {
                position: relative;
                width: 100%;
                padding: 20px;
                border-radius: $border-radius;

                & + .page-title {
                    margin-top: 10px;
                }

                i {
                    @include centre('y');
                    right: 10px;
                    font-size: 20px;
                }
            }
        } //.page-title__wrap--column
    } //.page-title__wrap

    span {
        font-family: $f-light;
        font-weight: 300;
        font-size: 20px;
        color: $gray;
    }

    &--small {
        display: block;
        margin-bottom: 15px;
        font-size: 18px;
        color: #000;
    } //.page-title__title

    &--link {
        color: $color-text--darkest;

        &:hover {
            color: $color-main;
            cursor: pointer;
        }

        i {
            margin-right: 10px;
        }
    }
} //.page-title

.devider {
    display: block;
    height: 1px;
    width: 100%;
    background-color: $color-bg--light;

    &--icon {
        height: auto;
        background-color: transparent;
        position: relative;
        @include flexCenter;
        justify-content: center;

        &::before,
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background-color: $color-icon;
        }

        .devider {

            &__icon {
                width: 120px;
                height: 40px;
                margin: $gutter * 1.5;
                font-size: 42px;
                color: $color-icon;
                fill: $color-icon;
            }
        }
    } //.devider--icon

    @each $theme, $color in $text-colors {

        &--#{$theme} {

            &::before,
            &::after {
                background-color: $color;
            }

            .devider {

                &__icon {
                    color: $color;
                    fill: $color;
                }
            }
        }
    }
} //.devider

.block {

    &--desktop {
        @include respond-to(xs) {
            display: none;
        }
    }

    &--mobile {
        @include min-screen(481) {
            display: none;
        }

        .card-info__rating {
            display: block;
            margin-bottom: 0;
        }
    }
} //.block mobile & desktop hidden

.policy {
    margin-top: 10px;
    @extend %textLight;
    font-size: 12px;
    line-height: 1.2;
    text-align: center;

    a {
        font-size: 13px;

        &:hover {
            text-decoration: underline;
        }
    }
} //.policy

.validate-message {
    padding: 15px;
    font-size: 14px;
    border-radius: $border-radius;

    &--error {
        color: $color-danger;
        background: #fbebee;
        border-color: rgba(247, 73, 105, 0.75);
    }

    &--info {
        color: $color-info;
        background: #ebf5fd;
        border-color: rgba(40, 131, 210, 0.75);
    }

    &--success {
        color: $color-success;
        background: #ebf5fd;
        border-color: rgba(40, 131, 210, 0.75);
    }
} //.validate-message

.meter {
    position: relative;

    &__bg {
        @include vy;
        left: 0;
        width: 92%;
        height: 2px;
        padding-right: 30px;
        background-color: $gray--lighten;
    } //.merter__inner

    &__index {
        display: block;
        height: 2px;
        background-color: $gray--light;
        @include vy;
        left: 0;
    } //.meter__index

    &__count {
        font-family: $f-light;
        font-size: 13px;
        @include vy;
        right: 0;
    } //.meter__count
} //.meter

.sorting {
    display: flex;
    align-items: center;

    &__title {
        white-space: nowrap;
        margin-right: 10px;
        font-size: 14px;
        @include respond-to(sm) {
            display: none;
        }
    } //.sorting__title

    .selectize-input {
        max-width: 160px;

        .item {
            display: block;
            @include text-overflow;
        }
    }
} //.sorting

.confirm {

    &.is-invalid {

        .confirm__error {
            display: block;
        }

        .bb-input__input {
            border: 1px solid $color-danger;
        }
    } //.confirm.is-invalid

    &__timer {
        margin-bottom: 10px;
        @extend %textLight;
        font-size: 13px;
        line-height: 1;

        span {
            font-family: $f-bold;
            font-size: 15px;
            line-height: 1;
            color: $gray--darkest;
        }
    } //.confirm__timer

    &__link {
        display: block;
        padding: 0;
        margin-bottom: 10px;
        line-height: 1;

        &:hover,
        &:focus {
            text-decoration: none;
        }
    } //.confirm__link

    &__field {
        margin: 10px 0;
        position: relative;
        @include flexCenter;

        .bb-input {
            width: auto;

            &__input {
                height: 40px;
                max-width: 95px;
                margin-right: 25px;
                padding-right: 16px;
                font-size: 23px;
            }
        }
    } //.confirm__field

    &__error {
        font-family: $f;
    } //.confirm__error

    &__btn {
        height: 40px;
        padding: 0 20px;
        text-transform: uppercase;
        font-family: $f-medium;
    } //.confirm__btn
} //.confirm

.map-pin {

    &__title {
        display: block;
        margin-right: 10px;
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 700;
        color: #000;

        &:hover {
            color: $color-main;
        }
    } //.map-pin__title

    &__address {
        display: block;
        margin-bottom: $gutter / 1.5;
        @extend %textLight;
        font-size: 14px;
    } //.map-pin__address

    &__image {
        margin-bottom: $gutter / 1.5;
    } //.map-pin__image

    &__info {
        @include flexCenter;
    } //.map-pin__info

    &__fav {
        margin-left: auto;
    } //.map-pin__fav

    &__articul {
        display: inline-block;
        margin-left: 10px;
        vertical-align: middle;
        font-size: 13px;
        color: $gray--light;
    } //.map-pin__articul

    &__properties {
        margin-top: 8px;

        .properties {

            &__icon {
                width: 20px;
                height: 20px;
            }

            &__item {

                & + .properties__item {
                    margin-left: 15px;
                }
            }
        }
    } //.map-pin__properties

    &__hover {
        font-size: 14px;

        .rating {
            margin: 5px 0;
        }

        span {
            display: block;
            font-size: 12px;
        }
    } //.map-pin__hover
} //.map-pin

.counter {
    @extend %badge;
    min-width: 25px;
    font-size: 11px;
    color: $gray--darkest;
    background-color: $color-main;
} //.counter

.counter-text {
    padding: 3px 5px;
    font-family: oswald-500, sans-serif;
    font-size: 10px;
    background-color: $gray--lighten;
    position: absolute;
    right: 5px;

    &--input {
        top: 50%;
        transform: translateY(-50%);
    } //.counter-text--unput

    &--textarea {
        bottom: 5px;
    } //.counter-text--textarea
} //.counter-text

.metro-icon {
    display: inline-block;

    &::before {
        content: '';
        display: inline-block;
        width: 13px;
        height: 9px;
    }

    @each $colors in more, blue, blue_white, gray, green, grown, muted, orange,
        purpure, red, salat, yellow
    {

        &--#{$colors} {

            &::before {
                background-image: url('../img/general/metro/metro--#{$colors}.png');
            }
        } //.metro-icon
    }
} //.metro-icon

.working {

    &__list {
        @extend %box;
        @include flexCenter;
        @include respond-to(sm) {
            border: 1px solid $color-border--lightest;
            box-shadow: none;
        }
        @include respond-to(xs) {
            border-bottom-color: transparent;
            flex-wrap: wrap;
            align-items: flex-start;
        }
    } //.working__list

    &__item {
        width: 14.4%;
        @extend %textLight;
        font-size: 14px;
        border-right: 1px solid $color-border--lightest;
        @include flexColumn;
        justify-content: center;
        align-items: center;

        &:last-child {
            border-right-color: transparent;
        }
        @include respond-to(xs) {
            width: 50%;
            text-align: center;
            border-bottom: 1px solid $color-border--lightest;
            flex-direction: row;

            &:last-child {
                width: 100%;
            }

            &:nth-child(2n) {
                border-right-color: transparent;
            }
        }

        &.is-holiday {
            color: $color-main;
        }
    } //.working__item

    &__day {
        width: 100%;
        padding: 7px 10px;
        text-align: center;
        border-bottom: 1px solid $color-border--lightest;
        @include respond-to(xs) {
            width: auto;
            border: none;
        }
    } //.working__day

    &__time {
        padding: 7px 10px;
        font-family: $f-num;
        @include respond-to(xs) {
            width: 95px;
        }
    } //.working__time

    &__empty {
        @extend %textLight;
        font-size: 15px;
    } //.working__empty
} //.working

.seoblock {
    @include respond-to(xs) {
        display: none;
    }

    h2,
    h4 {
        display: block;
        margin-bottom: $gutter;
        font-size: 23px;
        font-family: $f-medium;
        font-weight: 500;
        color: $gray--darkest;
    } //.seoblock__title

    h4 {
        font-size: 17px;
    } //.seoblock__subtitle

    p {
        display: block;
        margin-bottom: 15px;
        font-family: $f-light;
        font-size: 14px;
    } //.seoblock__desc

    ul {
        margin-bottom: 15px;

        li {
            display: block;
            margin-bottom: 5px;
            font-family: $f-light;
            font-size: 14px;
            color: #000;
            @include listIcon;
        }
    } //.seoblock__list
} //.seoblock

.promo-block {
    padding: 15px 20px;
    border-radius: $border-radius;
    @include cover;
    position: relative;
    @include flexCenter;

    @include respond-to(xs) {
        padding: 20px 10px;
        text-align: center;
        flex-direction: column;
    }

    &__left {
        flex: 1;
    } //.promo-block__left

    &__right {
        width: auto;

        @include respond-to(xs) {
            width: 100%;

            .btn {
                display: block;
            }
        }
    } //.promo-block__right

    &__title {
        font-family: $f-medium;
        font-weight: 500;
        margin-bottom: 0;
        font-size: 22px;
        color: #000;

        @include respond-to(xs) {
            margin-bottom: 10px;
            font-family: $f-bold;
            font-size: 20px;
        }
    } //.promo-block__title

    &__desc {
        font-size: 15px;
        color: $gray--darkest;

        @include respond-to(sm) {
            font-size: 14px;
        }

        @include respond-to(xs) {
            margin-bottom: 10px;
            font-size: initial;
        }
    } //.promo-block__desc

    &__btn {
        // text-transform: uppercase;
        font-family: $f-medium;
        box-shadow: 1px 1px 10px rgba(#000, 0.3);
    } //.promo-block__btn
} //..promo-block

.list {

    &__item {
        display: block;
        padding-left: 23px;
        margin-bottom: 5px;
        @extend %textLight;
        font-size: 13px;
        position: relative;
    } //.list__item

    &__icon {
        font-size: 14px;
        color: $gray--light;
        position: absolute;
        top: 3px;
        left: 0;

        &.fa-clock {
            left: 1px;
        }
    } //.list__icon

    &__text {
        margin-left: 10px;
    } //.list__text

    &__dot {
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
    } //.list__dot

    &--dot {

        .list {

            &__item {
                padding-left: 0;
            }

            &__text {
                margin-left: 0;
            }
        }
    } //.list--dot

    &--vertical-dot {

        .list__item {
            font-family: $f;
            font-size: 16px;
            padding-left: 0;
        }

        .list__item + .list__item {
            margin-top: 0;

            &::before {
                content: '';
                display: block;
                width: 7px;
                height: 7px;
                border-radius: 50%;
                margin: 7px auto;
                background-color: $color-main;
            }
        }
    } //.list--vert-dot

    &--dots {

        .list__item {
            margin-bottom: 15px;
            padding-left: 10px;
            font-family: $f;
            font-size: 17px;
            position: relative;

            &::before {
                content: '';
                background-color: $color-main;
                border-radius: 50%;
                position: absolute;
                top: 10px;
                left: 0;
                width: 5px;
                height: 5px;
            }
        } //.list--bb-dot .list__item
    } //.list--bb-dot

    &--progress {
        counter-reset: list;

        @include respond-min(md) {
            margin-bottom: 60px;
            @include row-flex;
        }

        & > .list__item {
            padding-left: 40px;
            margin-left: 30px;
            margin-bottom: 0;
            list-style-type: none;
            position: relative;

            @include respond-min(md) {
                margin-left: 0;
                margin-top: 80px;
                @include col;
                @include size(4);
            }

            &:not(:last-child) {
                padding-bottom: 35px;
                border-left: 2px dashed $color-main;

                @include respond-min(md) {
                    padding-bottom: 0;
                    border: none;
                }
            }
        }

        .list {

            &__title {
                font-family: $f-bold;
                font-weight: 600;
                font-size: 18px;
                margin-bottom: 14px;

                @include respond-min(md) {
                    margin-bottom: 20px;
                    font-size: 22px;
                }

                &::before {
                    counter-increment: list;
                    content: counter(list);
                    display: block;
                    width: 35px;
                    height: 35px;
                    padding-top: 5px;
                    color: #fff;
                    text-align: center;
                    border-radius: 50%;
                    background-color: $color-main;
                    position: absolute;
                    top: -5px;
                    left: -18px;

                    @include respond-min(md) {
                        @include centre('x');
                        top: -200px;
                        padding: 0;
                        font-size: 300px;
                        color: $color-main;
                        background-color: transparent;
                        opacity: 0.07;
                    }
                }
            }
        }
    } //.list--progress

    &--square {

        .list__item {
            padding-left: 20px;

            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                top: 7px;
                left: 0;
                width: 10px;
                height: 10px;
                background-color: $color-main;
                border-radius: $border-radius;
            }

            & + .list__item {
                margin-top: 20px;
            }
        }
    } //.list--square

    &--large {

        .list__item {
            font-family: $f;
            font-size: 18px;
        }
    } //.list--large

    &--dashed {

        .list {

            &__item {
                @include listIcon;

                &::before {
                    top: 9px;
                }
            }
        }
    } //.list--dashed

    &--icon {

        .list {

            &__item {
                padding-left: 0;
                margin-bottom: $gutter / 2;
                @include flexCenter;

                &:last-child {
                    margin-bottom: 0;
                }

                @include respond-to(i5) {
                    margin: 0 0 $gutter / 2;
                }

                strong {
                    margin: 0 3px;
                }
            }

            &__icon {
                position: static;
                font-size: 20px;
            }

            &__text {
                font-size: 15px;
            }
        }
    } //.list--icon

    &--devider {

        .list {

            &__item {

                &:first-child {

                    .list__icon {

                        &::after {
                            right: -46px;
                            @include respond-to(i5) {
                                right: -38px;
                            }
                        }
                    }
                }

                &:last-child {

                    .list__icon {

                        &::after {
                            display: none;
                        }
                    }
                }
            }

            &__icon {

                &::after {
                    content: '';
                    display: block;
                    width: 70px;
                    height: 1px;
                    background-color: $color-border--lighten;
                    position: absolute;
                    top: 10px;
                    right: -41px;

                    @include respond-to(i5) {
                        width: 50px;
                        right: -20px;
                    }
                }
            }
        }
    } //.list--devider

    &--row {
        @include row-flex;
        justify-content: center;

        .list {

            &__item {
                @include col;
                @include size(4);
                @include flexColumn;
                align-items: center;
            }

            &__icon {
                margin-bottom: $gutter / 2;
                margin-right: 0;
            }
        }
    } //.list--row

    &--inline {
        @include flexCenter;

        .list {

            &__item {

                & + .list__item {
                    margin-left: 10px;
                }
            }
        }
    } //.list--inline

    &-color {
        @each $theme, $color in $button-colors {

            &--#{$theme} {

                .list {

                    &__icon {
                        color: $color;

                        &::after {
                            background-color: $color;
                        }
                    }
                }
            }
        }
    } //.list-color
} //.list

.category {
    width: 100%;

    &.is-checked {

        .category {

            &__dropdown {
                display: block;
            }
        }
    }

    &__title {
        @extend %titleSmallBold;
    } //.category__title

    &__item {
        margin-bottom: 3px;
        padding: 0;
        border-radius: $border-radius;
        cursor: pointer;
        overflow: hidden;
        position: relative;

        @include respond-to(sm) {
            display: inline-block;
            margin-right: 10px;
            margin-bottom: 5px;
            font-family: $f-light;
            text-align: center;
        }

        &:hover {
            @include min-screen(480) {
                background-color: $color-bg--light;
            }

            .category__link {
                color: #000;
            }

            .category__price {
                left: 0;
            }
        }

        &.is-selected {
            display: inline-block;
            padding: 2px 17px 2px 6px;
            background-color: $color-bg--light;
            @include max-screen($checkbox-transform-window-width) {
                padding: 0;
                background-color: transparent;
            }

            .category {

                &__link {
                    display: inline-block;
                    @include max-screen($checkbox-transform-window-width) {
                        color: #fff;
                        border: none;
                        background-color: $color-main;
                    }
                }

                &__icon {
                    display: inline-block;
                    @include max-screen($checkbox-transform-window-width) {
                        display: none;
                    }
                }
            }
        }

        &--more {
            display: block;
            margin: 0;
            padding: 3px;
            border: 0;
        }
    } //.category__item

    &__link {
        display: block;
        padding: 1px 2px 2px;
        font-size: 14px;
        color: $gray--darkest;

        @include respond-to(sm) {
            display: inline-block;
            padding: 4px 15px;
            text-align: center;
            font-family: $f-light;
            font-size: 15px;
            border: 1px solid #e0e0e0;
            border-radius: 50px;
            background-color: transparent;
        }
    } //.category__link

    &__price {
        padding-top: 4px;
        text-align: center;
        background-color: $color-bg--light;
        @include tr;
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        z-index: 99;

        i {
            font-size: 11px;
        }

        @include respond-to(xs) {
            display: none;
        }
    } //.category__price

    &__icon {
        display: none;
        margin-left: 5px;
        font-size: 13px;
        color: $color-main;
        opacity: 0.7;
        cursor: pointer;
        position: absolute;
        top: 1px;
        right: 4px;
        z-index: 99;

        &::after {
            content: '\f00d';
            @include fa('regular');
        }

        &:hover {
            opacity: 1;
        }
        @include respond-to(xs) {
            display: none;
        }
    } //.category__icon

    &__dropdown {
        display: none;
        margin-top: 10px;
        @include respond-to(xs) {
            margin-top: 0;
        }

        .catalog-filter__title {
            display: block;
        }

        .bb-checkbox {
            display: block;
            margin: 0;
            @include text-overflow;
        }
    } //.category__dropdown

    &--inline {

        .category {

            &__title {
                @include min-screen(769) {
                    display: none;
                }
            }

            &__list {
                @include respond-to(sm) {
                    display: flex;
                    flex-wrap: wrap;
                }
            }

            &__item {
                display: inline-block;
                margin-right: 5px;
                margin-bottom: 0;
                padding: 3px 15px;
                background-color: $color-bg--light;

                @include respond-to(sm) {
                    margin-top: 6px;
                    padding: 3px 30px;
                }

                &:hover {
                    background-color: darken($color-bg--light, 7%);
                }

                .category__icon {
                    display: inline-block;
                    @include respond-to(sm) {
                        width: 30px;
                        height: 100%;
                        font-size: 19px;
                        right: 0;
                    }
                }
            }

            &__link {
                display: inline-block;
                padding: 0;
                @extend %textLight;
                @include respond-to(sm) {
                    border: none;
                    border-radius: $border-radius;
                }
            }
        }
    } //.category--inline

    &--line {

        .category {

            &__list {
                @include flexCenter;
            }

            &__item {
                text-align: left;
                cursor: inherit;

                &:hover {
                    background-color: transparent;
                }

                & + .category__item {
                    margin-left: $gutter / 2;
                }
            }
        }
    } //.category--list

    &--bg {

        .category {

            &__list {
                @include row-flex;
            } //.category--bg .category__list

            &__item {
                min-height: 60px;
                margin-top: 10px;
                @include cover;
                @include col;
                @include size(3);
                @include size-xs(6);

                @include respond-to(xs) {
                    min-height: 70px;
                }

                &:hover {

                    .bb-checkbox__box {
                        border-color: #fff;
                    }
                }
            } //.category--bg .category__item
        } //.category--bg .category

        .bb-checkbox {

            &__box {
                margin-right: 0;
                border-color: #fff;
                top: 5px;
                left: auto;
                right: 5px;
            } //.category--bg .bb-checkbox__box

            &--radio {

                input {

                    &:checked {

                        & + .bb-checkbox__box {
                            border-color: #fff;

                            &::before {
                                background-color: #fff;
                            }
                        }
                    }
                }
            } //.category--bg .bb-checkbox--radio
        } //.category--bg .bb-checkbox
    } //.category--bg

    &-color {
        @each $theme, $color in $button-colors {

            &--#{$theme} {

                .category__item {
                    color: $color;
                    background-color: transparent;
                    border-radius: 30px;
                    border: 1px solid lighten($color, 20%);

                    &:hover {
                        background-color: transparent;
                    }

                    &--more {
                        padding: 3px;
                        margin: 0;
                        border: 0;
                        color: $gray--light;
                        cursor: pointer;

                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    } //.category-color
} //.category

.info-block {
    display: inline-block;
    margin: -10px;
    padding: 10px;
    position: relative;
    cursor: help;

    &::before {
        display: block;
        min-width: 20px;
        min-height: 20px;
        content: '\f05a';
        @include fa('light');
        font-size: 19px;
        line-height: 1;
        color: $color-icon;
        text-align: center;
        @include tr;
    }

    &:hover {

        &::before {
            color: $gray--dark;
        }
    }

    &__list {
        display: block;
        margin-bottom: 10px;

        &-title {
            font-family: $f-bold;
            font-size: 14px;
        } //.info-block__list-title

        li {
            font-size: 14px;
            @include listIcon(9px);
        }

        input[type='file'] {
            display: none;
        }
    } //.info-block__list

    &__item {
        padding-left: 15px;
        position: relative;

        @include respond-min(sm) {

            &:not(:last-child) {
                border-bottom: 1px solid #f1f1f1;
            }
        }

        @include respond-to(sm) {
            cursor: pointer;
        }

        &::before {
            content: '';
            display: block;
            width: 8px;
            height: 2px;
            background-color: $color-main;
            position: absolute;
            top: 10px;
            left: 0;
        }
    } //.info-block__item

    &__link {
        padding: 6px 15px;
        color: $color-text;
        font-family: $f;
        font-size: 14px;
        transition: initial;
        @include flexCenter;

        &:hover {
            color: #000;
            background-color: #f9f9f9;
        }

        &.text-color--red {
            color: #f67c93;
        }
    } //.info-block__link

    &__icon {
        margin-right: 7px;
        font-size: 13px;

        &.fa-trash-alt {
            margin-right: 10px;
        }
    } //.info-block__icon

    &__text {
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.info-block__text

    &--large {

        .bb-dropdown {

            &__list {
                max-width: 480px;
            }
        } //.info-block__text
    } //.info-block--large

    &--more {
        cursor: pointer;

        &::before {
            content: '\f142';
            @include fa('regular');
            font-size: 24px;
            color: $color-icon-darken;
        }

        .info-block {

            &__list {
                margin-bottom: 0;
            }

            &__item {
                padding-left: 0;
                cursor: pointer;

                &::before {
                    display: none;
                }
            }
        } //.info-block--more .info-block

        .bb-dropdown {

            &__list {
                padding: 0;
            } //.info-block--more .bb-dropdown__list
        }
    } //.info-block--more

    &--arrow {
        cursor: pointer;

        &::before {
            content: '\f107';
            font-size: 16px;
        }

        .info-block {

            &__list {
                margin-bottom: 0;
            }

            &__item {
                padding-left: 0;
                cursor: pointer;

                &::before {
                    display: none;
                }
            }
        } //.info-block--more .info-block

        .bb-dropdown {

            &__list {
                padding: 0;
            } //.info-block--more .bb-dropdown__list
        }
    } //.info-block--more

    &--rotate {

        &::before {
            @include rotate(90);
        }
    } //.info-block--rotate
} //.info-block

.text-block {
    margin-bottom: 40px;
    @include row-flex;
    justify-content: center;

    @include respond-to(md) {
        margin-bottom: 20px;
    }

    &__item {
        margin-bottom: 30px;

        @include respond-to(xs) {
            margin-bottom: 50px;
        }

        @include flexColumn;
        align-items: center;

        @include col;
        @include size(4);
        @include size-sm(12);

        @include respond-to(sm) {
            max-width: 320px;
        }
    } //.text-block__item

    &__icon {
        width: 119px;
        height: 119px;
        margin-bottom: 20px;
        display: flex;

        &.h50 {
            @include respond-to(xs) {
                height: 80px;
            }
        }
    } //.text-block__icon

    &__title {
        margin-bottom: 20px;
        font-size: 25px;
        font-family: $f-medium;

        @include respond-to(md) {
            margin-bottom: 10px;
            font-size: 22px;
        }
    } //.text-block__title

    &__sub {
        font-family: $f;
        font-weight: 400;
        font-size: 17px;
        line-height: 1.5;
        text-align: center;
    } //.text-block__sub
} //.text-block

.user {

    &__inner {
        @include flexCenter;
    } //.user__inner

    &__avatar {
        display: inline-block;
        margin-bottom: $gutter;
        position: relative;

        &-add {
            width: 25px;
            height: 25px;
            color: #999;
            border: 0;
            border-radius: 50%;
            background-color: $color-main;
            cursor: pointer;
            outline: none;
            position: absolute;
            bottom: 5px;
            right: 0;
            @include tr;
            @include flexCenter;
            justify-content: center;

            &:hover {
                color: $gray;
            }

            .icon {
                width: 11px;
                height: 11px;
                fill: #fff;
                @include tr;
            }
        } //.user__avatar-add
    } //.user__avatar

    &__icon {
        margin: auto;
        font-size: 11px;
        color: #fff;
    } //.user__icon

    &__img {
        display: block;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
    } //.user__img

    &__name {
        display: block;
        line-height: 1;
        color: $gray--dark;
    } //.user_name

    &__online,
    &__position {
        display: block;
        margin-bottom: 10px;
        font-size: 13px;
        color: $gray--light;
    } //.user__online

    &__phone {
        color: $gray--light;
        font-size: 12px;
    } //.user__phone

    &__info-block {
        margin-left: auto;
    } //.user__info-block

    &__status {
        font-size: 13px;
        font-family: $f;
        color: $gray--light;

        &::before {
            display: none;
        }

        &_icon {
            display: inline-block;
            width: 6px;
            height: 6px;
            margin: 0;
            padding: 0;
            border-radius: 50%;
            position: relative;
            top: -1px;
        }

        &.is-success {

            .user__status_icon {
                background-color: $color-success;
            }
        }

        &.is-error {

            .user__status_icon {
                background-color: red;
            }
        }
    } //.user__status

    &--small {

        .user {

            &__img {
                width: 40px;
                height: 40px;
                margin-right: $gutter / 2;
            } //.user--small .user__img

            &__name {
                font-size: 15px;
            } //.user--small .user__name

            &__position {
                margin-bottom: 0;
            } //.user--small .user__position
        } //.user--small .user
    } //.user--small
} //.user

.control {

    &__list {
        @include flexCenter;
    } //.control__list

    &__item {
        padding: 0 7px;
        cursor: pointer;
    } //.control__item

    &__link {

        &:hover {

            .control {

                &__text,
                &__icon {
                    color: $gray--darkest;
                }
            }
        }
    } //.control__link

    &__text {
        @extend %textLight;
        color: $gray--light;
    } //.control__text

    &__icon {
        color: $gray--light;
        font-size: 16px;
    } //.control__icon
} //.control

.view-toggle {
    cursor: pointer;
    overflow: hidden;
    position: relative;
    background-color: $color-bg--light;
    border-radius: $border-radius;
    box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
        0 1px 3px 0 rgba(0, 0, 0, 0.2) inset;
    @include flexCenter;

    @include respond-to(xs) {
        display: inline-flex;
    }

    &.is-active {

        .view-toggle__item {

            &:first-child {
                background-color: $color-bg--lighten;
                border: 1px solid $gray--lighten;
                box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                    0 1px 3px 0 rgba(0, 0, 0, 0.2);

                .icon {
                    fill: $gray--light;
                }
            }

            &:last-child {
                border: none;
                background-color: $color-bg--light;
                box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                    0 1px 3px 0 rgba(0, 0, 0, 0.2) inset;

                .icon {
                    fill: $color-main;
                }
            }
        }
    } //.view-toggle.is-active

    &__item {
        width: 36px;
        height: 36px;
        overflow: hidden;
        position: relative;
        @include flexCenter;

        &:last-child {
            background-color: $color-bg--lighten;
            border: 1px solid $gray--lighten;
            box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                0 1px 3px 0 rgba(0, 0, 0, 0.2);

            .icon {
                width: 20px;
                height: 20px;
                margin: auto;
                fill: $gray--light;
            }
        }

        .icon {
            width: 20px;
            height: 20px;
            margin: auto;
            fill: $color-main;
        }
    } //.view-toggle__item
} //.view-toggle

.beauty-box {
    color: $color-text--darkest;
    font-family: encode-700, sans-serif;
    white-space: nowrap;
    line-height: 1;

    span {
        color: $color-main;
    } //.beauty-box span

    &--white {
        color: #fff;
    } //.beauty-box--white
} //.beauty-box

.mouse-icon {
    border: 2px solid black;
    border-radius: 16px;
    height: 40px;
    width: 26px;
    display: block;
    z-index: 10;
    opacity: 0.7;

    .wheel {
        animation-name: drop;
        animation-duration: 1s;
        animation-timing-function: linear;
        animation-delay: 0s;
        animation-iteration-count: infinite;
        animation-play-state: running;
        position: relative;
        border-radius: 10px;
        background: black;
        width: 2px;
        height: 6px;
        top: 4px;
        margin-left: auto;
        margin-right: auto;
    }

    &--white {
        border-color: #fff;

        .wheel {
            background: #fff;
        }
    } //.mouse-icon --white
} //.mouse-icon

.service-combo-text {

    span {
        display: inline-block;

        & + span {
            margin-left: 10px;

            &::before {
                margin-right: 10px;
                content: '\f067';
                @include fa('light');
                font-size: 13px;
                color: $color-icon;
            }
        }
    }
}

.empty {
    @include flexColumn;
    align-items: center;
    justify-content: center;

    &__block {
        margin-bottom: $gutter;
        @include flexCenter;

        .empty__text {
            margin-right: $gutter;
        }
    } //.empty__block

    &__title {
        display: block;
        margin-bottom: 10px;
        color: $gray--darken;
        font-family: $f-light;
        font-size: 26px;
        text-align: center;

        @include respond-to(xs) {
            font-size: 21px;
        }
    } //.empty__title

    &__text {
        font-family: $f;
        font-size: 15px;
    } //.empty__text

    &__icon {
        width: 70px;
        height: 70px;
        fill: $color-icon;
        color: $color-icon;
        font-size: 100px;

        &--medium {
            width: 90px;
            height: 90px;
        }
    } //.empty__icon

    &--inline {
        flex-direction: row;

        .empty__icon {
            margin-right: $gutter;
        }
    } //.empty--inline

    &--centre {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
    } //.empty--centre

    .button-box {
        @include flexCenter;

        .btn {

            & + .btn {
                margin-left: $gutter;
            }

            &-link {
                padding: 13px 0;
            }
        } //.empty .button-box .btn

        &--column {

            .btn {
                margin: 0;

                & + .btn {
                    margin-left: 0;
                }
            }
        } //.empty .button-box--column
    } //.empty .button-box
} //.empty

@keyframes drop {

    0% {
        top: 5px;
        opacity: 0;
    }

    30% {
        top: 10px;
        opacity: 1;
    }

    100% {
        top: 25px;
        opacity: 0;
    }
} //.mouse-icon animate

@keyframes animate {

    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(220deg);
    }
} //animation loader

@keyframes animate2 {

    0% {
        box-shadow: inset $color-main 0 0 0 17px;
        transform: rotate(-140deg);
    }

    50% {
        box-shadow: inset $color-main 0 0 0 2px;
    }

    100% {
        box-shadow: inset $color-main 0 0 0 17px;
        transform: rotate(140deg);
    }
} //animation loader

@keyframes pulse {

    0% {
        box-shadow: 0 0 0 0 rgba($color-main, 0.4);
    }

    70% {
        box-shadow: 0 0 0 10px rgba($color-main, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba($color-main, 0);
    }
} //animation pulse
