.beauty-link {

    .firstscreen {

        &__subtitle {
            margin-bottom: 0;
        } //.firstscreen__subtitle

        &__inner {
            padding-bottom: 70%;

            @media (min-height: 811px) and (max-width: 375px) {
                padding-bottom: 87%;
            }

            @include respond-min(md) {
                padding-bottom: 0;
            }
        } //.firstscreen__inner

        &__wrapper {
            position: relative;
        } //.firstscreen__wrapper

        &__btn {
            position: absolute;
            bottom: 30px;
        } //.firstscreen__btn
    } //.firstscreen

    .screen {

        &--img {
            height: 600px;
            overflow: hidden;
        } //.screen--img

        &--adv {

            .screen__title {
                margin-bottom: 40px;
            }
        } //.screen--adv
    } //.screen

    .icon-list {

        &__title {

            @include respond-min(md) {
                font-size: 25px;
            }
        } //.icon-list__title

        &__item {
            align-items: flex-start;

            @include respond-min(md) {
                align-items: center;
            }
        } //.icon-list_item

        &__icon {
            width: 60px;

            @include respond-min(md) {
                width: 70px;
            }

            img {
                width: 50px;
                height: 50px;

                @include respond-min(md) {
                    width: 70px;
                    height: 70px;
                }
            }

            &--all-messengers {

                img {
                    height: 60px;
                }
            } //.icon-list__all-messengers
        } //.icon-list__icon
    } //.icon-list
} //.beauty-link

.fullscreen-slider {

    &__video {
        position: relative;
        width: 100%;
        overflow: hidden;

        video {
            position: absolute;
            top: 0;
            left: -650px;
            height: 100%;

            @media (min-height: 811px) and (max-width: 375px) {
                left: -700px;
            }

            @include respond-min(md) {
                left:-19px;
            }
        }

    }

    .slick {

        &-track {
            height: 100%;
        } //.slick-track

        &-list {
            height: 100%;
        } //.slick-list

        &-slide {
            margin: 0;

            div {
                height: 100%;
            }
        } //.slick-slide

        &-dots {
            bottom: 40%;

            @include respond-min(md) {
                bottom: 16%;
            }

            li {
                margin: 0 3px;

                button::before {
                    width: 16px;
                    height: 16px;
                    transform: scale(1);
                    background-color: white;
                }
            }
        } //.slick-dots
    } //.slick
} //.fullscreen-slider
