.page-onepage {

    .wrapper {

        &__inner {
            z-index: initial;
        }
    } //.page-onepage .wrapper

    .header {

        &__inner {
            height: 50px;
            @include flexCenter;
            border: 0;
        } //.header__inner

        &__logo {
            margin-top: -4px;

            .logo__link {
                font-family: $f-medium;
                font-size: 15px;
            }
        } //.header__logo
    } //.page-onepage .header

    .logo {
        display: inline-block;
        margin-top: -4px;
        font-family: encode-700, sans-serif;
        font-size: 18px;
        letter-spacing: 1px;

        @include respond-to(sm) {
            margin-right: 15px;
        }

        @include respond-to(sm) {
            margin-right: 0;
            flex: 1;
        }

        @include respond-to(i5) {
            font-size: 17px;
        }

        &__img {
            width: 130px;
        } //.logo__img

        span {
            color: $color-main;
        } //.logo span
    } //.page-onepage .logo

    .nav {
        margin-left: auto;

        &__link {
            line-height: 50px;
        }
    } //.page-onepage .nav

    .nav-dropdown {
        top: 50px;
    } //.page-onepage .nav-dropdown

    .toggle-menu {
        display: flex;
    } //.toggle-mnu

    .footer {
        padding: 15px 0;
        background-color: $color-bg--lighten;
        border-top: 0;

        @include respond-to(sm) {
            padding: 13px 0;
        }

        &__logo {
            font-size: 14px;
            display: flex;
            justify-content: center;
            align-items: baseline;
        } //.footer__logo

        &__top {
            padding-top: 0;
        } //.footer__top

        &__bottom {
            padding-bottom: 0;
        } //.footer__bottom

        .logo {
            margin-left: 7px;
            flex: initial;

            &__link {
                color: $gray--darkest;
            }
        } //.footer .logo
    } //.footer

    &:not(.page-onepage--about) {

        .card {

            &__content {
                background-color: transparent;
            }
        }
    }

    &--home {

        .onepage {
            @include respond-to(sm) {
                background-color: #fff;
            }
        }

        .logo {

            &__link {
                color: #fff;
            }
        }

        .toggle-menu {

            &__icon {
                background-color: #fff;
            }
        }

        .page-title {
            @include respond-to(sm) {
                width: 100%;
                margin: 0;
                font-size: 16px;
                text-align: center;
            }

            &__wrap {
                @include respond-to(sm) {
                    margin-bottom: 0;
                    background-color: $color-bg--lighten;
                }
            }

            &--link {
                @include respond-to(sm) {
                    padding: $gutter 0;
                }
            }
        } //.onepage--home .page-title

        .footer {
            padding: 10px 0 13px;

            @include respond-to(sm) {
                background-color: $color-bg--lighten;
            }
        }
    } //.onepage--home

    &--about,
    &--reviews {

        .onepage {
            padding-top: 50px;
        }

        @include respond-to(sm) {

            .card-info {

                &__user {
                    @include respond-to(sm) {
                        display: none;
                    }
                }

                &__social {
                    @include respond-to(sm) {
                        display: none;
                    }
                }
            }
        }

        .footer {
            padding: 10px 0 13px;
        }
    }

    &--about {
        @include respond-min(md) {

            .toggle-mnu {
                margin-left: -4px;
            }
        }

        @include respond-to(sm) {

            .page-title {
                text-align: left;

                &__wrap {
                    margin: 0 0 15px;
                    padding: 0;
                    background-color: transparent;
                    align-items: flex-start;
                }
            }

            .card {

                &__content {
                    margin-top: 10px;
                }
            }

            .footer {
                background-color: #fff;
            }
        }

        .onepage {
            @include respond-to(sm) {
                background-color: #e0e0e0;
            }

            .card {

                &__content {
                    margin-top: 0;
                }
            }

            & > .container {
                padding: 0 8px;

                @include respond-to(sm) {
                    padding: 0;
                }
            }
        }
    } //.onepage--about

    &--registration {

        .screen {

            .container {
                @include respond-to(xs) {
                    padding: 0 20px;
                }
            }
        }

        .form {
            padding: 0;

            @include respond-min(xs) {
                padding: 15px 30px 30px;
            }
        }

        .footer {
            padding: 0;
        }
    } //.onepage--registratio
} //.onepage

.onepage {
    background-color: #fff;

    &__gallary {
        margin-bottom: $gutter;
        padding-bottom: 5px;
        background-color: #fff;

        @include respond-to(sm) {
            margin-bottom: 10px;

            .bb-slider {

                &__arrow {
                    display: none !important;
                }
            }

            .slick-slide {
                margin: 0;
            }
        }
    } //.onepage__gallary

    &__social {
        display: none;

        @include respond-to(xs) {
            display: block;
        }
    } //.onepage__social

    .page-title {
        @include respond-to(sm) {
            margin: 0;
            font-size: 16px;
        }
    } //.onepage .page-title

    .user {
        @include flexCenter;

        @include respond-to(sm) {
            margin-bottom: 0;
            padding-bottom: 0;
            border: 0;
        }

        &__avatar {
            margin-right: 15px;
            margin-bottom: 0;
        } //.onepage .user__avatar

        &__img {
            width: 50px;
            height: 50px;
        }

        &__name {
            font-family: $f-medium;
            font-size: 16px;
            line-height: 1.2;
            color: $gray--darkest;
            @include respond-to(xs) {
                font-size: 15px;
            }
        }
    } //.onepage .user

    .card {

        &__content {
            margin-top: 25px;
            // background-color: transparent;

            @include respond-min(md) {
                margin-top: 55px;
            }

            .bb-accordeon__item {
                margin-top: 0;

                @include respond-min(md) {
                    margin-bottom: 10px;
                }

                @include respond-to(sm) {
                    border-top: 0;
                    border-left: 0;
                    border-right: 0;
                }
            } //.onepage .card__content .bb-accordeon__item
        }

        &__about {
            @include respond-to(sm) {
                // margin-bottom: $gutter;
                // padding: 0;
                // box-shadow: none;
                // background-color: transparent;
            }
        }

        &__about_text {
            // padding: 10px 15px;
            // @extend %box;

            @include respond-to(xs) {
                // margin: 0 -10px;
                // @include border(top);
                // @include border(bottom);
                // box-shadow: none;
            }
        } //.onepage .card__about_text

        &-services {
            @include respond-to(sm) {
                margin-bottom: 1px;
            }
        }
    } //.onepage .card

    .card-info {
        top: 0;

        &__top {
            padding: 0;
            border: 0;
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;
            overflow: initial;

            &.is-booked {

                .card-info__btn {
                    @include trfY(0);
                }
            }
        } //.onepage .card-info__top

        &__bg {
            // margin: 2px;
            padding: 15px;
            @extend %box;
            @include respond-to(sm) {
                padding: 0;
                margin: 0;
                box-shadow: none;
            }
        } //.onepage .card-info__bg

        &__category {
            margin-bottom: 13px;
            padding-top: $gutter;
            border-top: 1px solid $color-border--lightest;

            @include respond-to(sm) {
                border-top: 0;
            }
        } //.onepage .card-info__category

        &__social {
            width: 100%;
            margin: auto;
        } //.onepage .card-info__social

        &__btn {
            @include respond-to(sm) {
                display: block;
                // border-radius: $border-radius;
                // width: 90%;
                // max-width: 414px;
                // position: fixed;
                // bottom: 65px;
                // left: 50%;
                // @include trfX(-50%);
                // transform: translate(-50%, 200%);
                @include trfY(100%);
                @include tr;
            }
        } //.onepage .card-info__btn
    } //.onepage .card-info

    .card-adress {
        @include respond-to(sm) {
            // &__inner {
            //     padding: 0;
            //     box-shadow: $bxs-box;
            //     background-color: #fff;

            //     .place-text {

            //         &__wrap {
            //             padding: 13px 15px 10px;

            //             & + .place-text__wrap {
            //                 border-top: 1px solid $color-border--lightest;
            //             }
            //         }

            //         &__box {
            //             margin-bottom: 0;
            //         }
            //     }
            // }

            // .working__list {
            //     box-shadow: $bxs-box;
            //     border: 0;
            // }
        }

        &__place {
            @include respond-to(sm) {
                margin-bottom: 10px;
            }
        }

        &__map {
            @include respond-to(sm) {
                box-shadow: none;
            }
        }
    } //.onepage .card-adress__working

    .card-about {
        // background-color: transparent;
    } //.onepage .card-about

    .card-request-form {

        &__top {
            margin-top: 0;
        }
    } //.onepage .card-request-form

    .social {

        &__inner {
            @include flexCenter;
            justify-content: center;
        }

        &__devider {
            max-width: 100px;
            margin: $gutter / 1.5 auto;
        }

        .bb-title--devider {
            width: 100%;
            margin: 30px auto;

            @include respond-to(sm) {
                margin: 20px 0;
            }

            &::before,
            &::after {
                width: 38%;
            }
        } //.onepage .social .bb-title--devider
    } //.onepage .social

    .button-social,
    .btn-social {
        padding: 10px;

        @include respond-to(sm) {

            &::before {
                font-size: 20px;
            }

            &-text {
                padding: 10px 0;
            }
        }

        & + .button-social {
            margin-left: $gutter / 2;
        }

        & + .btn-social {
            margin-left: $gutter / 2;
        }
    }

    .bb-accordeon {

        &__item,
        &__title {
            @include respond-to(sm) {
                border-radius: 0;
                box-shadow: none;
            }
        }
    } //.onepage .bb-accordeon
} //.onepage

.icon-list {
    margin-left: 0;
    @include row-flex;

    @include respond-min(xs) {
        display: flex;
        justify-content: center;
    }

    .icon-list__item {
        @include col;
        @include size(4);
        @include size-sm(6);
        @include size-xs(12);
    }

    &__item {
        margin-bottom: 30px;
        position: relative;
        @include flexCenter;

        @include respond-min(xs) {
            flex-direction: column;
        }
    } //.icon-list__item

    &__icon {
        width: 50px;
        height: 50px;
        margin-right: 20px;
        border: 2px solid #fff;
        border-radius: 50%;
        @include flexCenter;

        @include respond-min(xs) {
            width: 70px;
            height: 70px;
            margin-bottom: 20px;
            margin-right: 0;
            display: flex;
            justify-content: center;
        }

        @include respond-min(md) {
            margin-bottom: 20px;
        }

        i {
            margin: auto;
            font-size: 20px;

            @include respond-min(sm) {
                font-size: 30px;
            }
        }
    } //.icon-list__icon

    &__svg {
        width: 50px;
        height: 60%;
        fill: #fff;

        @include respond-min(xs) {
            width: 70px;
        }
    } //.icon-list__svg

    &__title {
        font-family: $f-medium;
        font-size: 20px;

        @include respond-min(xs) {
            text-align: center;
            font-size: 25px;
        }

        @include respond-min(sm) {
            font-size: 30px;
        }

        @include respond-min(md) {
            font-size: 35px;
        }
    } //.icon-list__title

    &__sub {
        font-family: $f-light;
        font-size: 16px;

        @include respond-min(xs) {
            font-size: 18px;
            text-align: center;
        }

        @include respond-min(sm) {
            font-size: 22px;
        }
    } //.icon-list__sub

    &--white {
        color: #fff;
    } //.icon-list--white
} //.icon-list

.float-card {
    position: relative;
    max-width: 375px;
    width: 80%;
    margin: auto;
    padding: 25px;
    background-color: #fff;
    border-radius: $border-radius;

    &__sub {
        margin-bottom: 10px;
        color: $color-text--light;
    } //.float-card__sub

    &__img {
        max-width: 200px;
        margin: 0 auto 20px;
        @include flexCenter;
        justify-content: center;

        &--rounded {

            img {
                border-radius: 50%;
            }
        } //.tab__img--rounded
    } //.float-card__img

    &__title {
        margin-bottom: 30px;
        text-align: center;
        color: $color-text--darkest;

        @include respond-min(md) {
            font-size: 25px;
        }
    } //.float-card__title

    &__price {
        @include flexCenter;
        justify-content: center;
        font-family: $f-bold;
        font-weight: 500;
        font-size: 22px;
        color: $color-main;

        @include respond-min(md) {
            font-size: 30px;
        }

        & + .float-card__note {
            margin-top: 0;
            margin-bottom: 20px;
        }

        .small {
            margin-left: 3px;
            font-family: $f;
            font-size: 16px;

            @include respond-min(md) {
                font-size: 20px;
            }
        }
    } //.float-card__price

    &__note {
        margin: 10px auto 5px;
        text-align: center;
        font-size: 13px;
        color: $color-text--light;
    } //.float-card__note

    &__desc {
        text-align: center;
        color: $color-text--darkest;
    } //.float-card__desc

    &__btn {
        width: 100%;
        margin-top: 25px;
    } //.float-card__btn
} //.float-card

.video {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    @include cover;

    @include respond-min(md) {
        margin-left: 0;
    }

    &__btn {
        background-color: transparent;
        border: 0;
        outline: none;
        @include centre;

        &::before {
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            background-color: #fff;
            @include centre;
            z-index: -1;
        }

        i {
            color: red;
            font-size: 50px;
        }
    } //.video__btn

    iframe,
    .youtube {
        @include coverdiv;
    } //.video

    &__wrapper {
        @include respond-min(md) {
            width: 1000px;
            margin: auto;
        }
    } //.registration .video__wrapper
} //.video

.client-logo {
    margin: auto;

    @include respond-to(sm) {
        flex-basis: 30%;

        img {
            margin: auto auto 30px;
            width: 55%;
        }
    }

    @include respond-to(xs) {

        img {
            width: 90%;
            margin: 0;
        }
    }

    &__wrap {
        @include flexCenter;
        margin-top: 30px;

        @include respond-to(sm) {
            margin-top: 0;
            flex-wrap: wrap;
        }
    }
} //.client-logo
