.onescreen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    @include sectionOverlay();

    &__inner {
        @extend .container;
        @include flexColumn;
        justify-content: center;
        align-items: center;
        flex: 1;

        @include respond-to(xs) {
            width: 100%;
        }
    } //.onescreen__inner

    &__text {
        margin-bottom: 30px;
        @include flexColumn;
        justify-content: center;
        align-items: center;
        width: 100%;

        @include landscape {
            margin-right: 30px;
        }
    }

    &__form {
        max-width: 414px;
        margin: 0 auto;
    } //.onescreen__form

    &__title {
        margin-bottom: 30px;
        text-align: center;
        line-height: 1.5;
        font-size: 30px;
        color: #fff;

        @include respond-min(md) {
            font-size: 40px;
        }

        &--large {
            font-size: 40px;

            @include respond-min(md) {
                font-size: 50px;
            }
        }
    } //.onescreen__title

    &__desc {
        text-align: center;
        font-size: 18px;
        color: #fff;
    } //.onescreen__desc

    &__footer {
        width: 100%;
        padding: 10px 0;
        background-color: rgba(0, 0, 0, 0.4);
        display: flex;
        justify-content: center;

        @include landscape {
            display: none;
        }

        .icon-list {
            margin-left: 0;
            margin-right: 0;

            &__link {
                width: 40px;
                height: 40px;
                margin-right: 0;
                line-height: 1;
                border: 2px solid #fff;
                border-radius: 50%;
                transition: initial;
                @include flexCenter;

                @include respond-min(xs) {
                    margin-bottom: 0;
                }

                &:hover {
                    color: $color-main;
                    border-color: $color-main;
                    cursor: pointer;

                    i {
                        color: $color-main;
                    }
                }

                & + .icon-list__link {
                    margin-left: 20px;

                    @include respond-min(md) {
                        margin-left: 50px;
                    }
                }

                i {
                    margin: auto;
                    font-size: 16px;
                    color: #fff;

                    // @include respond-min(sm) {
                    //     font-size: 25px;
                    // }

                    // @include respond-min(md) {
                    //     font-size: 20px;
                    // }
                }

                .fa-phone {
                    // transform: scale(-1, 1);
                    // font-size: 16px;

                    // @include respond-min(sm) {
                    //     font-size: 20px;
                    // }

                    // @include respond-min(md) {
                    //     font-size: 17px;
                    // }
                }

                .fa-instagram {
                    font-size: 19px;

                    // @include respond-min(sm) {
                    //     font-size: 27px;
                    // }

                    // @include respond-min(md) {
                    //     font-size: 22px;
                    // }
                }
            } //.icon-list__icon .icon-list
        } //.onescreen__footer
    } //.onescreen__footer

    &--bg-center {
        background-position: 27%;
    } //.onescreen--bg-center

    .form {
        width: 100%;
        padding: 30px;
        background-color: rgba(0, 0, 0, 0.4);

        &__link {
            color: #fff;

            &:hover {
                color: $color-main;
            }
        } //.form__link

        &__btn {
            box-shadow: none;
        } //.form__btn

        .bb-checkbox {
            color: #fff;

            &:hover {
                color: $color-main;
            }
        }
    } //.form

    &__button-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 500px;
        width: 100%;

        .btn {
            position: relative;
            width: 100%;
            padding: 20px 0;
            outline: none;
            border: 1px solid #fff;
            background-color: rgba(0, 0, 0, 0.3);
            font-size: 16px;
            color: #fff;

            & + .btn {
                margin-top: 15px;
            }

            &:hover {
                background-color: rgba(255, 255, 255, 0.35);
                cursor: pointer;
            }
        } //.btn
    } //.button-box
} //.onescreen
