.review {

    &__img {
        width: 50px;
        height: 50px;
        margin-top: -7px;
        margin-left: 15px;
        border-radius: 50%;
        float: right;
        overflow: hidden;
    } //.review__img

    &__sub {
        margin-bottom: 55px;
        font-family: $f-light;
        font-size: 16px;
        color: $color-text--light;
        line-height: 1.7;
    } //.review__sub

    &__name {
        text-align: right;
        font-size: 14px;
        font-family: $f-medium;
        color: $color-text--darkest;
    } //.review__author

    &__company {
        line-height: 1.5;
        text-align: right;
        font-size: 14px;
    } //.review__company
} //.review
