.screen {
    padding: 35px 0 45px;

    @include respond-min(md) {
        padding: 45px 0 60px;
    }

    &__title {
        position: relative;
        color: $gray--dark;
        font-family: $f-medium;
        font-weight: 500;
        font-size: 25px;
        line-height: 1.3;
        margin-bottom: 20px;
        text-align: center;

        @include respond-min(xs) {
            margin-bottom: 35px;
        }

        @include respond-min(sm) {
            line-height: 1.5;
            font-size: 35px;
        }

        &.mb-20 {
            margin-bottom: 20px;
        }

        &--white {
            color: #fff;
        } //.screen__title--white
    } //.screen__title

    &__subtitle {
        max-width: 60%;
        margin: auto;
        margin-bottom: 30px;
        text-align: center;
    } //.screen__subtitle

    &__desc {
        margin-bottom: 40px;
        font-family: $f;
        font-size: 16px;
        line-height: 1.7;
        color: $color-text;
        text-align: center;

        @include respond-min(md) {
            font-size: 20px;
        }

        p + p {
            margin-top: 10px;
        }

        &.mb-15 {
            margin-bottom: 15px;
            line-height: 1;

            @include respond-to(sm) {
                line-height: 1.4;
            }
        }
    } //.screen__description

    &__icon-slider {
        position: relative;

        @include screen(768px, 1024px) {
            max-width: 70%;
            margin: auto auto 20px;
        }

        @include respond-min(md) {
            width: 350px;
            margin: 0;
            order: 1;
            margin-right: auto;
        }

        .bb-slider__slide + .bb-slider__slide {
            @include respond-min(md) {
                margin-top: 20px;
            }
        }
    } //.screen__icon-slider

    &__mockup {
        position: relative;
        width: 75%;
        max-width: 300px;
        margin-top: -61%;

        @include respond-min(md) {
            margin-top: -10%;
        }

        @media (min-height: 811px) and (max-width: 375px) {
            margin-top: -75%;
        }
    } //.screen__mockup

    &__img {
        max-width: 450px;
        width: 80%;
        margin: auto;
    } //.screen__img

    &__btn {
        display: block;
        width: 100%;
        margin-top: 40px;
        font-size: 17px;

        @include respond-min(xs) {
            max-width: 320px;
            margin: 30px auto 0;
        }
    } //.screen__btn

    &--img {
        @include sectionOverlay;
        @include cover;

        & > * {
            z-index: 1;
            color: #fff;
        }

        .screen {

            &__title,
            &__desc {
                color: #fff;
            }
        }

        &.no-overlay {

            &::before {
                background-color: transparent;
            }
        }

        .btn-default {

            &:not(.form__btn):not(.float-card__btn) {
                box-shadow: 0 2px 5px 0 $gray--dark;
            }
        }
    } //.screen--img

    &--get-icons {

        .container {
            @include respond-min(md) {
                max-width: 900px;
            }
        }

        .screen__inner {
            margin-bottom: 20px;

            @include respond-min(md) {
                display: flex;
                align-items: center;
                margin-bottom: 60px;
            }
        }

        .list--square {
            margin-bottom: 50px;
            @include respond-min(md) {
                margin-right: 60px;
                margin-bottom: 0;
            }
        }
    } //.screen--get-icons

    &--icons {

        .screen__slider {
            @include respond-min(xs) {
                max-width: none;
                width: 100%;
            }
        }
    } //.screen--icon-collect

    &--adv {

        .text-block {

            &__wrap {
                @include respond-min(md) {
                    flex-direction: row;
                    align-items: baseline;
                }
            }
        }

        img {
            @include respond-min(md) {
                width: 80%;
                margin: auto;
            }
        }

        .screen__img {
            @include respond-min(md) {
                display: block;
                width: 100%;
            }

            img {
                width: 80%;
                margin: auto;
            }
        }
    } //.screen--advantages

    &--reg {
        padding: 15px 0;

        @include respond-min(md) {
            display: none;
        }
    } //.screen--reg

    &--reviews {

        .bb-slider__slide {
            @include respond-min(md) {
                padding: 0 30px;
            }
        }
    } //.screen--reviews

    &--site {

        .container {
            @include respond-min(md) {
                width: 1000px;
            }
        } //.screen--site .container

        .screen {

            &__inner {
                align-items: center;
                @include flexColumn;

                @include respond-min(sm) {
                    @include row-flex;
                    flex-direction: row;

                    .mockup-slider {
                        margin: auto;
                    }
                }
            } //.screen--site .screen__inner

            &__text {
                @include flexColumn;
                align-items: center;
                justify-content: center;

                @include respond-min(xs) {
                    @include col;
                    @include size(7);
                    @include size-xs(12);
                }

                .text-block {

                    &__item {
                        max-width: 430px;
                        @include size(12);

                        @include respond-min(md) {
                            margin-bottom: 25px;
                        }
                    }

                    &__icon {
                        @include respond-min(md) {
                            width: 100px;
                            height: 100px;
                            margin-bottom: 10px;
                        }
                    }

                    &__title {
                        @include respond-min(md) {
                            font-size: 20px;
                        }
                    }
                }
            } //.screen--site .screen__text
        } //.screen--site .screen

        .text-block {

            &__sub {
                @include respond-min(md) {
                    max-width: 320px;
                }
            } //.screen--site .text-block__sub
        } //.screen--site .text-block
    } //.screen--site

    &--why-we {

        .text-block {
            justify-content: center;
            align-items: flex-start;

            @include respond-min(md) {
                margin-bottom: 0;
            }
        }
    } //.screen--why-we

    &--course {

        .icon-list__icon {
            border: 1px solid #fff;

            @include respond-min(md) {
                border: 2px solid #fff;
            }
        }
    } //.screen--course

    &--speaker {

        .float-card {

            &__title {
                margin-bottom: 5px;
                font-family: $f-bold;
                font-size: 20px;
                // border-bottom: 1px solid $color-border--lighten;
            } //.screen--speaker .float-card__title

            &__sub {
                display: block;
                text-align: center;
            }

            &__desc {
                padding-top: 10px;
                @include border(top);

                p + p {
                    margin-top: 10px;
                }
            } //.screen--speaker .float-card__sub
        } //.screen--speaker .float-card
    } //.screen--speaker

    &--sale {

        .btn-link {
            width: 100%;
            text-align: center;
        }
    } //.screen--sale

    &--mockup {
        position: relative;
        z-index: 100;
        padding: 0;

        @include flex;
        justify-content: center;
    } //.screen--mockup

    &.bg-grey {
        background: $color-bg--lightest;
    } //.screen.bg-grey

    .list {

        &--pregress {
            margin-bottom: 30px;
        }
    } //.list

    .form {
        width: 100%;

        @include respond-min(md) {
            color: $color-text--darkest;
            background-color: rgba(255, 255, 255, 0.75);
            border-radius: $border-radius;
            margin: 0;
            margin-left: auto;
        }

        @include respond-to(sm) {
            padding: 10px 10px 20px;
        }

        @include respond-to(xs) {
            width: 100%;
            max-width: 100%;
        }

        &__title {
            width: 320px;
            font-family: $f-bold;
            font-size: 27px;
            line-height: 1.2;
            color: $gray--dark;
        }

        .bb-checkbox__box {
            border-color: $color-border;
        }

        .bb-checkbox--radio .bb-checkbox__title {
            font-size: 14px;
        }

        .bb-checkbox:hover .bb-checkbox__box {
            border-color: $color-main;
        }

        .bb-title--devider::after,
        .bb-title--devider::before {
            border-top: 1px solid $gray--light;
        }
    } //.form

    .button-box {

        .btn {
            min-width: 200px;

            & + .btn {
                margin-left: 15px;
            }
            @include respond-to(xs) {
                width: 100%;

                & + .btn {
                    margin-top: 20px;
                    margin-left: 0;
                }
            }
        }
    } //.screen .buttons-box

    .slick-dots {
        bottom: -24px;
    } //.screen .slick-dots
} //.screen

.video {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;

    @include respond-min(md) {
        margin-left: 0;
    }

    iframe {
        @include coverdiv;
    }
} //.video
