.header {
    color: $gray--darken;
    background-color: #fff;

    &__top {
        height: $base-header-size;
        position: relative;

        @include respond-to(xs) {
            height: $base-header-size--small;
        }
    } //.header__top

    &__bottom {
        height: 40px;
        @include respond-to(sm) {
            display: none;
        }
    } //.header__bottom

    &__inner {
        height: $base-header-size;
        border-bottom: 1px solid $color-border--lightest;
        @include flexCenter;

        @include respond-to(sm) {
            border-bottom: 0;
        }

        @include respond-to(xs) {
            height: $base-header-size--small;
        }

        &--order {
            display: flex;
            justify-content: center;

            @include respond-min(md) {
                background-color: #fff;
            }

            .header__title--order {
                flex: 1;
                @include flexCenter;
                justify-content: center;
                padding-right: 65px;
                margin-left: 0;
                font-family: $f-medium;
                color: #fff;
            }

            .button-link--icon {
                margin-right: auto;
                margin-left: 15px;

                i {
                    font-size: 20px;
                }
            }
        }
    } //.header__inner

    &__search {
        max-width: 320px;
        width: 100%;
        @include centre;
        z-index: z(fixed);

        @include respond-to(xs) {
            display: none;
        }
    } //.header__search

    &__city-select {
        margin-left: $gutter * 2;

        @include respond-to(sm) {
            margin-left: $gutter;
        }

        @include respond-to(xs) {
            display: none;
        }
    } //.header__city-select

    &__title {
        margin-left: 30px;
        font-family: $f-bold;

        @include respond-to(md) {
            margin-left: 10px;
        }

        @include respond-to(xs) {
            width: 50%;
            @include centre('y');
            left: 0;
            right: 0;
            margin: auto;

            color: #fff;
            font-family: $f-medium;
            font-size: 12px;
            letter-spacing: 0.5px;
            text-align: center;
            text-transform: uppercase;
            @include text-overflow;
        }
    } //.header__title

    &__logo {
        margin-top: 4px;
    } //.header__logo

    &--transparent {
        background-color: transparent;

        .toggle-mnu {

            span {
                background-color: #fff;

                &::before {
                    background-color: #fff;
                }

                &::after {
                    background-color: #fff;
                }
            }
        }

        .logo {

            &__link {
                color: #fff;

                &:hover {
                    color: #fff;
                }
            }
        }
    } //.header--transparent

    &--absolute {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: z(fixed);
    } //.header.bg--absolute

    &.header--fixed {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 201;
        // transition: left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);

        transition: transform 0.4s ease-in-out;
    } //.header.header--fixed

    &.bg--dark {
        border-bottom: none;
        background-color: $color-bg--dark;

        .header {

            &__top {
                background-color: $color-bg--dark;
            }

            &__bottom {
                background-color: #2f2f2f;
            }

            &__inner {
                border-bottom: none;
            }
        } //.header.bg--dark .header

        .city-select {

            &__title {
                color: $gray--light;
                border-color: $gray--light;

                &:hover {
                    color: $gray--lighten;
                    border-color: $gray--lighten;
                }
            }
        } //.header.bg--dark .change-city

        .header-user {
            color: $gray--light;

            &::after {
                color: $gray--light;
            }

            &:hover {
                background-color: $color-bg--darken;
            }

            &.is-active {
                background-color: $color-bg--darken;
            }
        } //.header.bg--dark .header-user

        .header-action {

            &__login {
                color: $gray--light;
            }
        } //.header.bg--dark .header-action

        .toggle-menu__icon {
            background-color: #fff;
        } //.header.bg--dark .toggle-menu__icon

        .request-info {

            &__icon {

                &::after {
                    border-color: $color-bg--dark;
                }
            }

            &:hover,
            &.is-active {
                background-color: $color-bg--darken;

                .request-info__icon {
                    color: $gray--light;

                    &::after {
                        border-color: $color-bg--darken;
                    }
                }
            }
        } //.header.bg--dark .request-info

        .logo {

            &__link {
                color: #fff;
            }

            span {
                font-family: encode-700, sans-serif;
                color: $color-main;
            }
        } //.header.bg--dark .logo

        .nav {

            &__item {

                &.is-active,
                &:hover {

                    .nav__link {
                        color: $gray--lightest;
                        background-color: #232323;
                    }
                }
            }

            &__link {
                color: $gray--lightest;
            }
        } //.header .bg--dark .nav
    } //.header.bg--dark

    .search {
        height: 35px;
        margin: 0 auto;
        flex: 1;
        display: flex;

        @include respond-to(sm) {
            display: none;
        }

        &__group {

            &--city {
                border-right: none;
                border-radius: $border-radius 0 0 $border-radius;
            }
        }

        &__input {
            height: auto;
            padding-left: 18px;
        } //.header .search__input

        &__btn {
            height: 35px;
            font-size: 15px;
            line-height: 35px;

            span {
                display: block;
                margin-top: 0;
                line-height: 1;
            }
        }
    } //.header .search
} //.header

.logo {
    display: inline-block;
    font-family: encode-700, sans-serif;
    font-size: 20px;
    letter-spacing: 1px;

    @include respond-to(sm) {
        margin-right: 15px;
    }

    @include respond-to(xs) {
        margin-top: -2px;
        margin-right: 0;
        flex: 1;
    }

    @include respond-to(i5) {
        font-size: 17px;
    }

    &__link {
        color: #000;

        &:hover {
            color: #000;
        }
    } //.logo__link

    &__img {
        width: 130px;
    } //.logo__img

    span {
        color: $color-main;
    } //.logo span
} //.logo

.wrapper {

    &:not(.page-cabinet) {

        .header-action {
            @include respond-to(sm) {
                margin-left: auto;
                margin-right: -10px;
            }
        }
    }

    &.page-cabinet {

        .request-info {

            &__items {
                height: calc(100vh - 193px);
            }
        }
    }
}

.header-action {
    margin-left: auto;
    @include flexCenter;

    @include respond-to(sm) {
        margin-left: auto;
    }

    &__inner {
        @include flexCenter;
    } //.header-action__inner

    &__request-info {

        .request-info {
            width: 60px;
            height: $base-header-size;

            @include respond-to(xs) {
                width: 50px;
                height: $base-header-size--small;
            }

            &__items {
                max-height: 355px;
                overflow-y: auto;

                @include respond-to(xs) {
                    height: calc(100vh - 142px);
                    max-height: 100%;
                }
            }
        }
    } //.header-action__request-info

    &__fav {
        width: 60px;
        height: $base-header-size;
        color: $color-text;

        @include respond-to(xs) {
            width: 50px;
            height: $base-header-size--small;
        }

        &:hover {
            color: $color-main;
        }
        @include respond-to(i5) {
            width: 35px;
        }
    } //.header-action__fav

    &__search {
        cursor: pointer;
        @include respond-min(sm) {
            display: none;
        }

        @include respond-to(xs) {
            width: 50px;
            height: 50px;
        }

        .icon {
            width: 21px;
            height: 21px;
        }
    } //.header-action__search

    &__login {
        color: $gray--dark;

        &:hover {
            color: $color-main;

            .icon {
                fill: $color-main;
            }
        }

        .icon {
            width: 28px;
            height: 28px;
            fill: $color-main;
            @include tr;
        }
    } //.header-action__login

    &__out {
        @include respond-to(xs) {
            display: none;
        }
    } //.header-action__out

    .button-icon {

        &--fav {

            &.is-checked {

                &::before {
                    color: $color-main;
                }
            }
        }
    }
} //.header-action

.header-user {
    height: $base-header-size;
    padding-right: 30px;
    padding-left: 12px;
    color: $gray--dark;
    cursor: pointer;
    position: relative;
    @include flexCenter;

    @include respond-to(sm) {
        padding: 0 10px;
    }

    @include respond-to(xs) {
        display: none;
        margin-left: 0;
        padding-left: 0;
    }

    &::after {
        content: '\f107';
        @include fa('light');
        color: $gray--dark;
        font-size: 15px;
        @include vy;
        right: 10px;
        @include tr;

        @include respond-to(sm) {
            display: none;
        }
    }

    &:hover {
        background-color: $color-gray--hover;
    }

    &.is-active {
        background-color: $color-gray--hover;

        .bb-dropdown__list {
            top: 60px;
        }
    }

    &__name {
        font-family: $f-medium;
        font-size: 14px;
        @include respond-to(sm) {
            display: none;
        }
    } //.header-user__name

    &__img {
        width: 35px;
        height: 35px;
        margin-left: 15px;
        border-radius: 50%;
        overflow: hidden;

        @include respond-to(sm) {
            margin-left: 0;
        }
    } //.header-user__img

    &__link {
        @include flexCenter;
    } //.header-user__link

    &__desc {
        font-family: $f-light-i;
        font-size: 13px;
    } //.header-user__desc

    .bb-dropdown {

        &__list {
            padding: 0;
            min-width: 180px;
            left: auto;
            right: 0;
            top: 70px;
            border-right: none;
            border-left: none;
            border-top-color: $color-border--lightest;
            border-bottom: 4px solid $color-main;
            box-shadow: 0 6px 20px rgba(#000, 0.3);

            &::before {
                border-bottom-color: $color-border--lightest;
            }

            ul {
                padding: 0;
                margin: 6px 0 1px;
            } //.header-user ul

            li {
                position: relative;

                &:last-child {

                    &::after {
                        content: '';
                        display: block;
                        width: 84%;
                        height: 1px;
                        background-color: $color-border--light;
                        @include vx;
                        top: -4px;
                    }

                    a {
                        margin-top: 7px;
                    }
                }

                &:hover {
                    background-color: $color-bg--lighten;

                    a {
                        color: #000;
                    }
                }
            } //.header-user li

            a {
                display: block;
                padding: 7px 15px 8px;
                font-size: 13px;
                color: $color-text;
                transition: initial;
            } //.header-user a
        } //.header-user .bb-dropdown__list
    } //.header-user .bb-dropdown

    &[data-dropdown-position^='right'] {

        .bb-dropdown__list {
            @include respond-to(sm) {
                right: 10px;
            }

            &::before,
            &::after {
                right: 42px;

                @include landscape {
                    right: 12px;
                }
            }
        } //.header-user[data-dropdown-position^='right'] .bb-dropdown__list
    }
} //.header-user

.request-info {
    position: relative;
    cursor: pointer;

    &:hover {
        background-color: $color-gray--hover;

        .request-info__icon {
            color: $gray;

            &::after {
                border-color: $color-gray--hover;
            }
        }
    }

    &.is-checked {

        .request-info {

            &__icon {

                &::after {
                    display: block;
                }
            }
        }
    }

    &.is-empty {

        .request-info {

            &__empty {
                display: flex !important;
            }

            &__footer,
            &__items {
                display: none;
            }
        }
    }

    &__header {
        display: block;
        padding: 0 10px;
        height: 50px;
        line-height: 50px;
        font-family: $f-medium;
        font-size: 15px;
        color: $gray--darkest;
        // border-bottom: 1px solid $color-border--lightest;
        @include border(bottom);

        @include respond-to(xs) {
            border-bottom: 0;
        }

        span {
            font-size: 14px;
            color: $gray--light;
        }
    } //.request-info__header

    &__footer {
        @include border(top);
        // background-color: $color-bg--lighten;

        .btn-link {
            display: block;
            height: 40px;
            line-height: 40px;
            padding: 0 10px;
            text-align: center;
            font-size: 13px;
            color: $color-text;
            @include flexCenter;
            justify-content: center;

            &:hover {
                text-decoration: none;
                background-color: #fbfbfb;
            }
        }//.request-info__footer .btn-link
    } //.request-info__footer

    &__inner {
        padding: 10px;
        overflow-x: hidden;
        @include flex;
        align-content: flex-start;

        .info-block {
            position: absolute;
            top: 10px;
            right: 10px;
        }
    } //.request-info__inner

    &__desc {
        margin-right: 35px;
        @include flexColumn;
        flex: 1;
    } //.request-info__desc

    &__icon {
        margin: auto;
        color: $gray--light;
        font-size: 20px;
        @include tr;
        @include vcenter;

        &::after {
            display: none;
            content: attr(data-count);
            @extend %badge;
            color: #000;
            background-color: $color-main;
            border: 2px solid #fff;
            position: absolute;
            top: -10px;
            left: 7px;
            animation: pulse 1.5s infinite;
            @include respond-to(xs) {
                animation: none;
            }
            @include respond-to(i5) {
                left: 7px;
            }
        }
    } //.request-info__icon

    &__items {
        overflow-x: hidden;
        background-color: $color-bg--lightest;
        @include bb-scroll--thin;
        @include smooth-scroll;
        @include flexColumn;
    } //.request-info__items

    &__item {
        background-color: #fff;
        border-bottom: 1px solid $color-border--lightest;
        cursor: pointer;
        position: relative;

        &:hover {
            background-color: #fbfbfb;

            .request-info__action {
                display: block;
            }
        }

        &:last-child {
            border-bottom: none;
        }
    } //.request-info__item

    &__avatar {
        display: inline-block;
        width: 35px;
        height: 35px;
        margin-right: 10px;
        border-radius: 50%;
        overflow: hidden;
        position: relative;
    } //.request-info__avatar

    &__text {
        margin-bottom: 3px;
        font-size: 13px;
        line-height: 1.3;
        color: $color-text;

        strong {
            font-family: $f-medium;
            font-weight: 500;
            color: #000;
        }
    } //.request-info__text

    &__title {
        display: block;
        color: $color-text--darkest;
        font-family: $f-medium;
        font-size: 14px;

        &:hover {
            color: $color-text--darkest;
        }
    } //.request-info__title

    &__time {
        display: block;
        font-size: 13px;
        line-height: 1;
        color: $gray--light;
    } //.request-info__time

    &__action {
        display: none;

        @include respond-to(xs) {
            display: block;
        }

        .bb-dropdown__list {
            padding: 5px 0;
        }
    } //.request-info__action

    &__empty {
        display: none !important;
        min-height: 250px;
    } //.request-info__empty

    &[data-dropdown-position^='right'] {

        &.is-active {
            background-color: $color-gray--hover;

            &::after {
                border-color: $color-gray--hover;
            }

            .request-info__icon {
                color: $gray;

                &::after {
                    border-color: $color-gray--hover;
                }
            }

            & > .bb-dropdown__list {
                top: 59px;
                z-index: 250;

                @include respond-to(xs) {
                    top: 50px;
                    right: -1px;
                }
            }
        } //.request-info.is-active

        & > .bb-dropdown__list {
            top: 70px;

            @include respond-to(xs) {
                top: 60px;
                right: -1px;
            }

            &::before,
            &::after {
                right: 23px;

                @include respond-to(xs) {
                    right: 19px;
                }
            }
        }
    }

    & > .bb-dropdown__list {
        min-width: 320px;
        max-width: 377px;
        padding: 0;
        left: auto;
        right: 1px;
        top: 70px;
        @include dropdownResponseWidth;

        @include respond-to(xs) {
            box-shadow: none;
            border-radius: 0;
            top: 60px;
            right: -11px;
        }

        &::before,
        &::after {
            right: 23px;

            @include respond-to(xs) {
                right: 29px;
            }
        } //.request-info .bb-dropdown__list
    } //.request-info .bb-dropdown
} //.request-info
