$input-title-size: 14px;

%isInvalid {

    .bb-input,
    .bb-date {

        &__input {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-text {

        &__field {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-select {

        select {
            border-color: rgba(247, 73, 105, 0.75) !important;
        }
    }

    .bb-input-tip {
        display: block;
        color: $color-danger;
        background: #fbebee;
        border-color: rgba(247, 73, 105, 0.75);

        &__arrow-up {

            &::before {
                background: #fbebee;
                border-color: rgba(247, 73, 105, 0.75);
            }
        }
    }

    .select2-container--default .select2-selection--single {
        border-color: rgba(247, 73, 105, 0.75) !important;
    }
}

%isInfo {

    .bb-input,
    .bb-date {

        &__input {
            border-color: rgba(40, 131, 210, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-text {

        &__field {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-select {

        select {
            border-color: rgba(40, 131, 210, 0.75) !important;
        }
    }

    .bb-input-tip {
        display: block;
        color: $color-info;
        background: #ebf5fd;
        border-color: rgba(40, 131, 210, 0.75);

        &__arrow-up {

            &::before {
                background: #ebf5fd;
                border-color: rgba(40, 131, 210, 0.75);
            }
        }
    }

    .select2-container--default .select2-selection--single {
        border-color: rgba(40, 131, 210, 0.75) !important;
    }
}

%isDisabled {

    .bb-input,
    .bb-select,
    .bb-text {

        &__title {
            color: $gray--lighten;
        }
    }

    .move-select__search {

        &::after {
            color: $gray--lighten;
        }
    }

    input {
        border-color: $gray--lighten;

        &:disabled {
            background-color: transparent !important;
        }
    }
}

@mixin inputTransform {
    @include respond-to(xs) {

        &::before {
            color: $color-icon;
            right: 0;
        }

        &.is-focus,
        &.is-invalid,
        &.is-info {

            .bb-input-tip {
                background-color: transparent;
            }
        }

        &.is-invalid {

            .bb-input {

                &__input {
                    border-left: 0;
                    border-right: 0;
                    border-top: 0;
                }
            }

            .select2-container {

                .select2-selection--single {
                    height: 37px;
                    line-height: 37px;
                }
            } //.aplication-success .select2-container

            .select2-container--default {

                .select2-selection--single {
                    border-left: 0;
                    border-right: 0;
                    border-top: 0;
                    border-bottom: 1px solid transparent;
                    border-radius: 0;
                    box-shadow: none;
                }
            } //.aplication-success .select2-container--default
        }

        &.is-focus {

            .bb-input,
            .bb-text {

                &__title {
                    top: -12px;
                    font-size: 12px;
                    color: $gray;
                }

                select,
                input,
                textarea {
                    border-bottom: 1px solid #000;
                }
            }

            &:not([data-type='selected']) {

                .bb-select {

                    &::before {
                        display: none;
                    }
                }
            }
        }

        .bb-input-born {

            &__select {

                &.is-focus {

                    .bb-input-born__title {
                        top: -12px;
                        font-size: 12px;
                        color: $gray;
                    }
                }
            }
        }

        .bb-input,
        .bb-input-born,
        .bb-text {

            &__title {
                margin-bottom: 0;
                font-family: $f;
                font-size: 16px;
                color: $gray--darken;
                box-shadow: none;
                position: absolute;
                top: 10px;
                z-index: 1;
                @include tr;
            }

            &__input {
                position: relative;
                z-index: 2;
                background-color: transparent;
            }
        }

        .bb-text {

            &__field {
                min-height: auto;
                height: 37px;
                padding: 7px 0 5px;
                font-size: 16px;
                line-height: 1.4;
                background-color: transparent;
                border-left: 0;
                border-right: 0;
                border-top: 0;
                border-radius: 0;
                overflow: hidden !important;
                position: relative;
                z-index: 2;

                &:focus {
                    box-shadow: none;
                    border-bottom: 1px solid #000;
                }
            }
        }

        .bb-select {

            &::before {
                font-size: 22px;
                right: 0;
            }
        }

        select,
        input,
        textarea {
            padding: 0 30px 0 0;
            border-radius: 0;
            border-top: 0;
            border-left: 0;
            border-right: 0;
            border-bottom: 1px solid $color-border--light;

            &:focus {
                box-shadow: none;
            }
        }

        select {
            height: 40px;
            line-height: 1;
            position: relative;
            z-index: 2;
            background-color: transparent;
        }

        .bb-input-tip {
            padding: 0;
            border: 0;

            &__arrow-up,
            .fa-times {
                display: none;
            }
        }

        .select2-container {

            .select2-selection--single {
                height: 37px;
                line-height: 37px;

                .select2-selection__rendered {
                    padding-left: 0;
                    padding-right: 25px;
                    line-height: 37px;
                    font-size: 16px;

                    .custom-select__results {

                        span {

                            &:first-child {
                                @include text-overflow;
                                flex-grow: 1;
                            }

                            &:not(:first-child) {
                                font-size: 14px;
                            }

                            &:nth-child(2) {
                                margin-left: auto;
                            }

                            &:nth-child(3) {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        } //.aplication-success .select2-container

        .select2-container--default {

            &.select2-container--open,
            &.select2-container--open.select2-container--below {

                .select2-selection--single {
                    border-color: transparent;
                    border-bottom-color: $color-main;

                    &:hover {
                        border-color: transparent;
                        border-bottom-color: $color-main;
                    }
                }
            }

            .select2-selection--single {
                border-left: 0;
                border-right: 0;
                border-top: 0;
                border: 1px solid transparent;
                border-radius: 0;
                box-shadow: none;
            }
        } //.aplication-success .select2-container--default
    }
}

.bb-input {
    display: inline-block;
    position: relative;

    &::before {
        @include fa('light');
        color: $color-icon--light;
        font-size: 13px;
        line-height: 1;
        @include vy;
        z-index: 1;
        right: 40px;
    } //.bb-input::beofer

    &__icon {
        padding: 5px 10px;
        color: $color-icon--light;
        font-size: 18px;
        line-height: 1;
        @include vy;
        right: 0;
        cursor: pointer;

        &.fa-eye,
        &.fa-eye-slash {
            padding-right: 5px;
        }
    } //.bb-input__icon

    &::after {
        position: absolute;
        top: 8px;
    } //.bb-input::after

    &.is-invalid {
        @extend %isInvalid;
    } //.bb-input.is-invalid

    &.is-error {

        &::after {
            content: '\f12a';
            @include fa('solid');
            color: $color-danger;
            position: absolute;
            right: 13px;
        }

        @extend %isInvalid;
    } //.bb-input.is-error

    &.is-info {
        @extend %isInfo;
    } //.bb-input.is-info

    &.is-complite {

        &::after {
            content: '\f00c';
            @include fa('regular');
            color: $color-success;
            right: 8px;
        }
    } //.bb-input.is-complite

    &.is-disabled {
        @extend %isDisabled;
    } //.bb-input.is-disabled

    &__wrapper {
        @include flex;
    } //.bb-input__wrapper

    &__wrap {
        position: relative;

        .bb-input + .bb-input {
            margin-left: $gutter;
        }

        &_inline {
            display: inline-block;

            & + & {
                margin-left: 30px;
                @include respond-to(xs) {
                    margin-left: 0;
                }
            }
        }
    } //.bb-input__wrap

    &__row {
        display: flex;

        .bb-input {
            width: 50%;

            & + .bb-input {
                margin-left: 20px;
            }
        }
    } //.bb-input__row

    &__title {
        margin-bottom: 5px;
        font-size: $input-title-size;

        span {
            display: inline-block;
            margin-left: 5px;
        }
    } //.bb-input__title

    &__input {
        @extend %styleInput;
    } //.bb-input__input

    &__box {
        display: inline-block;
        margin-right: 30px;
        position: relative;
        @include respond-to(xs) {
            margin-right: 0;
        }
        @include respond-to(i5) {

            span.js-remove-period {
                position: absolute;
                right: -30px;
                top: 46%;
            }
        }
    } //.bb-input__box

    &__remove {
        width: 16px;
        height: 16px;
        margin-left: 15px;
        fill: $gray--lighten;
        position: relative;
        top: 2px;
        cursor: pointer;
        @include tr;

        &:hover,
        &:focus {
            fill: $gray;
        }
        @include respond-to(xs) {
            width: 15px;
            height: 15px;
            margin-left: 0;
            margin-right: 5px;
        }
    } //.bb-input__remove

    &__error {
        display: none;
        font-family: $f-light;
        font-size: 13px;
        line-height: 1;
        color: $color-danger;
        @include text-overflow;
        position: absolute;
        left: 0;
        bottom: -15px;
        width: 100%;
    } //.bb-input__error

    &__message {
        padding: 10px 15px;
        font-size: 13px;
        line-height: 1.3;
        border-radius: 0 0 $border-radius $border-radius;
        box-shadow: $bxs-box;
        background-color: #fff;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        z-index: z(success);
    } //.bb-input__message

    &--title {
        @include flexColumn;

        &::before {
            top: auto;
            transform: translate(0);
            bottom: 12px;
            right: 10px;
        }

        &::after {
            top: 34px;
        }

        .bb-input {

            &__icon {
                top: auto;
                bottom: 12px;
                transform: translate(0);
            } //.bb-input--title .bb-input__icon
        }

        .counter-text {
            bottom: 5px;
        } //.bb-input--title .counter-text
    } //.bb-input--title

    &--user {

        &::before {
            content: '\f007';
            right: 12.5px;
        }
    } //.bb-input--user

    &--price {
        width: 110px;

        @include respond-to(i5) {
            width: 100px;
        }

        &::before {
            content: '\f158';
            right: 10px;
        }

        .bb-input {

            &__input {
                width: 100%;
            }
        }
    } //.bb-input--price

    &--time {

        &::before {
            content: '\f017';
            right: 10px;
        }

        .bb-input {

            &__input {
                padding-right: 30px;
                font-family: oswald-500, sans-serif;
            }
        }
    } //.bb-input--time

    &--email {

        &::before {
            content: '\f0e0';
            font-size: 16px;
            right: 11.5px;
        }
    } //.bb-input--email

    &--phone {

        &::before {
            content: '\f3cf';
            @include fa('solid');
            font-size: 18px;
            right: 13.5px;
            top: auto;
            transform: translate(0);
            bottom: 11px;
        }

        .bb-input {

            &__input {
                // font-family: oswald-500, sans-serif;
            }

            &__icon {
            }
        }
    } //.bb-input--phone

    &--born {
        max-width: 320px;
        width: 100%;
        @include flexColumn;
        align-items: flex-start;

        @include respond-to(xs) {
            max-width: 100%;
        }

