.firstscreen {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    @include sectionOverlay;
    @include cover;
    overflow: hidden;

    @include respond-min(sm) {
        height: 500px;
    }

    @include respond-min(md) {
        height: 800px;
    }

    &__inner {
        position: relative;
        display: flex;
        padding: 0 25px;
        color: #fff;
        z-index: 2;
        text-align: center;

        @include respond-min(md) {
            text-align: left;
            max-width: 1200px;
            width: 100%;
        }

        &.absolute {
            position: absolute;
            top: 0;
            left: 0;
        }
    } // .firstscreen__inner

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
    } // .firstscreen__wrapper

    &__title {
        margin-bottom: 20px;
        color: #fff;
        font-family: $f-medium;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);
        font-size: 20px;
        letter-spacing: 1px;
        line-height: 1.4;
        text-transform: uppercase;

        @include respond-min(i7) {
            font-size: 27px;
        }

        @include respond-min(md) {
            width: 100%;
            margin-bottom: 30px;
            font-size: 50px;
            line-height: 1.3;
        }
    } // .firstscreen__title

    &__subtitle {
        margin-bottom: 50px;
        font-family: $f-light;
        font-size: 18px;
        line-height: 1.4;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);

        @include respond-min(xs) {
            margin-bottom: 20px;
        }

        @include respond-min(md) {
            font-size: 20px;
        }

        @include respond-min(xs) {
            margin-bottom: 20px;
        }
    } // .firstcreen__subtitle

    &__footnote {
        font-family: $f-light;
        font-size: 14px;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);

        @include respond-min(md) {
            display: none;
        }
    } // .firstscreen__footnote

    &__btn {
        max-width: 320px;
        width: 100%;
        margin-top: 50px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
        font-size: 17px;
        align-self: center;

        @include respond-min(md) {
            display: none;
            align-self: auto;
        }

        &:hover {
            box-shadow: inherit;
            transform: none;
        }
    } // .firstscreen__btn

    &.inst-border {
        border: 10px solid transparent;
        //   ,     
        -moz-border-image: -moz-linear-gradient(top right, #962fbf 0%, #d62976 35%,#fa7e1e 70%,#ffdc3e 100%);
        -webkit-border-image: -webkit-linear-gradient(top right, #962fbf 0%, #d62976 35%,#fa7e1e 70%,#ffdc3e 100%);
        border-image: linear-gradient(to bottom left, #962fbf 0%, #d62976 35%,#fa7e1e 70%,#ffdc3e 100%);
        border-image-slice: 1;
    }

    .form {
        @include respond-to(md) {
            margin-left: 30px;
        }
    } //.firstscreen .form

    .mouse-icon {
        position: absolute;
        bottom: 60px;
        left: 50%;
        transform: translateX(-50%);

        @include respond-to(sm) {
            bottom: 20px;
        }

        @include respond-min(xs) {
            bottom: 60px;
        }
    } //.firstscreen .mouse-icon
} //.firstscreen
